function handleMouseMove(e) {
  if (!boxRef.current) return;

  // Increment fail counter
  setFails(prev => {
    const updated = prev + 1;
    updateMockingMessage(updated);
    return updated;
  });

  const rect = boxRef.current.getBoundingClientRect();
  const boxWidth = boxRef.current.offsetWidth;
  const boxHeight = boxRef.current.offsetHeight;

  const mouseX = e.clientX - rect.left;
  const mouseY = e.clientY - rect.top;

  let dx = pos.x + buttonWidth / 2 - mouseX;
  let dy = pos.y + buttonHeight / 2 - mouseY;

  const distance = Math.sqrt(dx * dx + dy * dy) || 1;

  const flickDistance = Math.random() * 60 + 100;
  dx = (dx / distance) * flickDistance;
  dy = (dy / distance) * flickDistance;

  let newX = pos.x + dx;
  let newY = pos.y + dy;

  newX = Math.max(0, Math.min(newX, boxWidth - buttonWidth));
  newY = Math.max(0, Math.min(newY, boxHeight - buttonHeight));

  setPos({ x: newX, y: newY });
}
